

















 	var view;
 	export default {
 		data() {
 			return {
 				isPassword: 'true',
 				username: '0000',
 				password: 'posun@2019',
 				loading: false,
 			};
 		},
 		created() {
 			view = this;
 		},
 		methods: {
 			login() {
 				if (!this.username) {
 					this.toast('请输入用户名')
 				} else if (!this.password) {
 					this.toast('请输入密码')
 				} else {
 					this.loading = true;
 					uni.setStorageSync('empid', this.username);
 					this.$request({
 						url: 'WXLOGIN',
 						method: 'POST',
						showLoading:true,
						loadingText:'正在提交',
 						data: {
 							openId: uni.getStorageSync('openid'),
 							empId: this.username,
 							password: this.password
 						},
 						success: (data) => {
							console.log(111)
							console.log(data)
 							getMenu();
 						},
 						complete() {
 							view.loading = false;
 						}
 					});
 				}
 			},
 		}
 	}
 	function getMenu() {
 		view.$request({
 			url: 'GETMENU',
 			showLoading: true,
 			loadingText: '正在获取菜单...',
 			data: {
 				empId: view.username,
 			},
 			success: (menu) => {				
				console.log(111)
				console.log(menu)
 				if (menu.length > 0) {
 					uni.navigateTo({
 						url: '../main/main'
 					})
 				} else {
 					uni.navigateTo({
 						url: '../main/main'
 					})
					//  					uni.showToast({
					//  						title: '系统未分配菜单，请联系管理员',
					//  						icon: 'none'
					//  					})
 				}
 			},
 		})
 	}
 